import  "./projectdescr.scss"
import {Link} from "react-router-dom";


export default function ProjectDescriptCard({image="https://images.pexels.com/photos/5082578/pexels-photo-5082578.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2",title,description,link}){
    return(
        <>
            <div className="project-descr">
                <div className="pd-picture">
                    <img src={image} className="pd-img" alt="Photo Missing"/>
                    <div className="pd-pic-text">{title}</div>
                </div>
                <div className="pd-text">
                    <div className="pd-text-text">
                        {description}
                    </div>
                    <div className="pd-link"><Link to={link}>OPEN PROJECT</Link></div>
                </div>
            </div>
        </>
    )
}
