import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import "./table.scss"


export default function MUITable(props) {
    return (
        <>
            <div className="sect">
                <TableContainer component={Paper} class="atable">
                    <Table sx={{ minWidth: 650 }} aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                <TableCell className="table-heading">PROJECTS</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            <TableRow>
                                <TableCell>{props.project}</TableCell>
                                <TableCell/>
                                <TableCell className="cell" ><div className="edit">Edit</div></TableCell>
                                <TableCell className="cell" ><div className="delete">Delete</div></TableCell>
                                {/*<TableCell className="delete">Delete</TableCell>*/}
                            </TableRow>

                            <TableRow>
                                <TableCell className="add">Add Project</TableCell>
                            </TableRow>
                        </TableBody>
                    </Table>
                </TableContainer>
            </div>


        </>
    )
}
