import "./homehero.scss"
import { Link } from 'react-scroll';


export default function HomeHero(){
    return(
        <>
            <div className="home-hero">
                <div className="heroWrapper">
                    <div className="main">DAVID TENDAI GONDO</div>
                    <div className="roles">Web Developer | Data Scientist | Business Analyst | UI/UX Designer</div>
                    <div className="btn"><Link to="myProjects" smooth={true} duration={500} className="btn">Explore My Work</Link></div>
                </div>
            </div>
        </>
    )
}