import "./projectshero.scss"
import Navbar from "../navbar/Navbar";

export default function ProjectsHero({ title="MY PROJECTS", desc}){
    return(
        <>
            <div className="hero">
                <Navbar/>
                <div className="text-section">
                    <div className="hero-title">{title}</div>
                    <div className="hero-text">{desc}</div>
                </div>

            </div>


        </>
    )
}

export  function ProjectHero({ title="MY PROJECTS", desc}){
    return(
        <>
            <div className="heros">
                <Navbar/>
                <div className="text-section-h">
                    <div className="hero-title">{title}</div>
                    <div className="hero-text">{desc}</div>
                </div>

            </div>


        </>
    )
}