import  "./styles.scss"
import Navbar from "../components/navbar/Navbar";
import {useState} from "react";
import SendEmail from "../components/form/SendEmail";

export default function Contact(){
    const[pop,setPop] = useState(false)
    const handleClick=()=>{
        setPop(true)
    }
    return(
        <>
            <div className="contact">
                <Navbar/>
                <div className="main-body">
                    <div className="main-body-text">
                        <div className="head">ABOUT ME</div>
                        <div className="more-text">
                            Hi there! My name is David Gondo, aka The Infinitimetrical. As a Computer Science student, I am passionate about leveraging technology to create effective digital solutions and provide data-driven insights. My goal is to create systems and products that optimize processes and deliver personal and business value. With my expertise in business analysis, data analysis, product management, and software development, I am the go-to person for identifying opportunity gaps and developing strategic digital products that fill those gaps. I am a certified analyst, holding certifications as a Business Analyst, Financial Analyst, and Data Analyst. Let's work together to create digital solutions that take your business to the next level.
                            { pop &&
                                <SendEmail/> }
                        </div>
                        <button className="btn" onClick={handleClick}>Send Me An Email</button>


                    </div>
                </div>
            </div>
        </>
    )
}