import ProjectsHero from "../components/projects-hero/ProjectsHero";
import Footer from "../components/footer/Footer";
import { Grid } from '@material-ui/core';



export default function Project({id,header,desc,tech,github,site,designs,images=["https://images.pexels.com/photos/3153204/pexels-photo-3153204.jpeg?auto=compress&cs=tinysrgb&w=600","https://images.pexels.com/photos/5082581/pexels-photo-5082581.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2"]} ){
    return(
        <>
            <div className="project">
                <ProjectsHero title={header}/>
                <div className="desc">
                    <div className="desc-title">Description</div>
                    <div className="desc-text">{desc}</div>
                </div>
                <div className="tech-used">
                    <div className="desc-title">Technologies Used</div>
                    <div className="tech-text">{tech}</div>
                </div>
                <div className="project-files">
                    <div className="desc-title">Project Files</div>
                    <ul className="files">
                        <li className="file-sect">GitHub Repository : <a href={github}>{github}</a></li>
                        <li className="file-sect">Site link : <a href={site}>{site}</a></li>
                        <li className="file-sect">Project Designs : <a href={designs}>{designs}</a></li>
                    </ul>
                </div>
                <div className="site-images">
                    <h2 className="desc-title">Designs</h2>
                    {
                        images.map((image)=>(
                            <img src={image} alt="Photo Missing" className="im-1"/>
                        ))
                    }
                </div>

                <Footer/>
            </div>


        </>
    )
}




