export function ProjectData({
                                id,
                                header,
                                desc,
                                tech,
                                github,
                                recommendations,
                                designs,
                                images=["https://images.pexels.com/photos/3153204/pexels-photo-3153204.jpeg?auto=compress&cs=tinysrgb&w=600","https://images.pexels.com/photos/5082581/pexels-photo-5082581.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2"],
                                rec=false,
                                projectFiles=[""]
                            }) {
    return (
        <>
            <div className="project">
                <ProjectsHero title={header} />
                <div className="desc">
                    <div className="desc-title">Description</div>
                    <div className="desc-text">{desc}</div>
                </div>
                <div className="tech-used">
                    <div className="desc-title">Methodology </div>
                    <div className="tech-text">{tech}</div>
                </div>
                <div className="project-files">
                    <div className="desc-title">Project Files</div>
                    <ul className="files">
                        <li className="file-sect">GitHub Repository: <a href={github} target="_blank">{github}</a></li>
                        {projectFiles.map((file, index) => (
                            <li key={index}>{file}</li>
                        ))}
                    </ul>
                </div>
                <div className="tech-used">
                    <div className="desc-title">Visualisations </div>
                    {/*<p className="file-sect">Visualisations: <a href={designs} target="_blank">{designs}</a></p>*/}
                    <div className="site-images">
                        {images.map((image) => (
                            <img src={image} alt="Photo Missing" className="im-1" />
                        ))}
                    </div>
                </div>
                {rec && (
                    <div className="recommendations">
                        <div className="desc-title">Recommendations/Findings</div>
                        <ul>
                            {recommendations.map((recom, index) => (
                                <li key={index}>{recom}</li>
                            ))}
                        </ul>
                    </div>
                )}
            </div>
            <Footer/>
        </>
    );
}


