import  "./form.scss"
import ClearIcon from "@mui/icons-material/Clear";
import {useState} from "react";

export default function EditForm(){
    const[visible,setVisible]=useState(true)
    return(
        <>

            <div className="add-form">
                <form className="add-wrapper">
                    <ClearIcon sx={{marginLeft : "45%",cursor:"pointer"}} className="cancel" onClick={()=>{setVisible(false)}}/>

                    <div className="sect">
                        <label className="label">Project Name</label>
                        <input className="input" placeholder="The Steward"/>
                    </div>
                    <div className="sect">
                        <label className="label">Project Description</label>
                        <textarea placeholder="Project Description......"/>
                    </div>
                    <div className="sect">
                        <label className="label">Technologies Used</label>
                        <input className="input" placeholder="Reactjs,CSS,PHP"/>
                    </div>
                    <div className="sects">
                        <div className="sect-link">
                            <label className="label-link">GitHub Link</label>
                            <input className="input-link" placeholder="github.com"/>
                        </div>
                        <div className="sect-link">
                            <label className="label-link">Site Link</label>
                            <input className="input-link" placeholder="www.site.com"/>
                        </div>
                        <div className="sect-link">
                            <label className="label-link">Behance Link</label>
                            <input className="input-link" placeholder="behance.com"/>
                        </div>
                    </div>
                    <input type="file" className="file" id="add" style={{ display: 'none' }}/>
                    <label htmlFor="add">
                        <button className="button">Add Images</button>
                    </label>

                    <button className="button submit" type="submit">Submit</button>

                </form>
            </div>
        </>
    )
}