import "./navbar.scss"
import {Link} from "react-router-dom";

export default  function Navbar(){
    return(
        <>
            <div className="navbar">
                <div className="navWrapper">
                    <ul className="items">
                        <li className="item"><Link to="/home" style={{textDecoration:"none"}} className="hov">Home</Link></li>
                        <li className="item"><Link to="/projects" style={{textDecoration:"none"}} className="hov">Projects</Link></li>
                        <li className="item"><Link to="/contact" style={{textDecoration:"none"}} >Contact Me</Link></li>
                        <li className="item"><a href="./assets/resume.pdf" style={{textDecoration:"none"}} target="_blank">Resume</a></li>
                    </ul>
                </div>
            </div>
        </>
    )
}