import Project, {ProjectData} from "./Project";

export  function ACLED(){
    return(
        <>
            <ProjectData
                header="ACLED Data Analysis"
                id="data1"
                desc="The Armed Conflict Location & Event Data Project aims to track armed conflict around the world by recording the groups attacking, where they occured and any other details about the conflict.The aim of this data analysis is to investigate if there any temporal patterns in conflict events, and how have these patterns changed over time"
                tech="I first did some wrangling to remove irrelevant columns and create new ones.Then carried on exploratory data analysis to find out any temporal patterns and to know which features to explore furthur.From the EDA ll as able to make some hypotheses which l then employed hypothesis testing(chi-squared test, t-test) to validate.Finally l created visualisations using plotly library and seaborn.Then created an A4 poster to show the main visualisations in one page "
                github="https://github.com/Stoichiometrical/ACLED-Data-Analysis"
                images={["/assets/Final.jpg"]}
                recommendations={["Address underlying grievances: Many conflicts in Africa are driven by socio-economic and political factors such as inequality, corruption, and lack of access to resources. Addressing these underlying grievances through policies and programs aimed at promoting economic growth, reducing corruption, and improving access to resources can help prevent conflicts from emerging in the first place.\n" +
                "\n" ,
                    "Promote peaceful protests: Protests have become the most common type of conflict in Africa. While some protests can turn violent, many are peaceful expressions of dissent. Governments should work to create an environment where peaceful protests can take place without fear of repression or violence. How can we create peaceful protest environments This can be achieved by : Ensure that citizens have the right to peaceful assembly and freedom of expression, as guaranteed by international human rights law. Train law enforcement officials on how to manage peaceful protests without resorting to violence. Encourage dialogue between protesters and government officials to address the root causes of grievance\n" +
                    "\n" ,
                    "Protect citizens from violence: Violence against citizens is one of the top conflicts in Africa. Governments should work to protect their citizens by providing security, strengthening law enforcement, and addressing human rights abuses. This can be done by : Strengthen law enforcement agencies and the judiciary to ensure that perpetrators of violence are held accountable. Provide training to security personnel on human rights and the use of force in accordance with international standards. Establish mechanisms for citizens to report human rights abuses and seek redress.\n" +
                    "\n" ,
                    "Build peace and reconciliation: In areas where conflict has already occurred, peacebuilding and reconciliation efforts can help promote healing and prevent future conflicts. This can involve measures such as promoting dialogue, providing reparations to victims, and supporting the reintegration of former combatants into society. This can be done by : Support the establishment of truth and reconciliation commissions to investigate past human rights abuses and promote healing. Provide support for the reintegration of former combatants into society, such as job training or access to education. Promote dialogue and cooperation between communities to build trust and reduce tensions."]}
                rec={true}
            />
        </>
    )
}

export  function Fifa(){
    return(
        <>
            <ProjectData
                header="FIFA World Cup 2022 Data Analysis"
                id="data1"
                desc="Analysis of FIFA World Cup 2022 Data. This analysis aims to find interesting facts and statistics about the world cup as well as an novel patterns in the data"
                tech="Python, 1 Sample t-Test , Data Visualization"
                gihub="https://github.com/Stoichiometrical/World-Cup-Data--Analysis"
                site=""
                images={["https://miro.medium.com/v2/resize:fit:1400/format:webp/1*EEOP3qYg_5lTTFnoLzjB9A.png"]}
                recommendations={["Lusail Iconic Stadium and Al Bayt Stadium had the most games played Most games had an equal amount of games being played at, this might suggest thatv they were used for different stages of the game",
                    "Cesar Ramos and Wilton Sampaio referred the most games in the FIFA World Cup 2022",
                    "The games refereed by Wilton Sampaio are : SENEGAL vs NETHERLANDS POLAND vs SAUDI ARABIA NETHERLANDS vs UNITED STATES ENGLAND vs FRANCE The games refereed by Cesar Ramos are : DENMARK vs TUNISIA BELGIUM vs MOROCCO PORTUGAL vs SWITZERLAND FRANCE vs MOROCCO",
                    "The number of passes a team makes does not determine match outcome.Teams with the highest number of passes didnt always win, infact there are more teams which had fewer passes but still won the game",
                    "Attendance depended on the stadium's capacity.Stadiums with higher capacities had more people . It is also observed that most games(about 60 out of 64 games) exceeed full capacity ie above 100% capacity Showing that there was signifcant show up to the world cup games Lusail Stadium had the highest attendences and was always filled above maximum capacity",
                    "There were 226 yellow cards and 4 red cards given pout during the 2022 FIFA World Cup",
                    " No team had more than 1 red card.There were only four teams which got red cards namely Morrocco, Netherlands, Cameroon and Wales .However 32 teams got yellow cards with Saudi Arabia and Netherlands having the most yellow cards(10).",
                    "The team with the most goals was France with 16 goals, followed by Argentina(15) and England(14)"
                ]}
                rec={true}
                projectFiles={["https://medium.com/@langatkevin/fifa-world-cup-2022-data-analysis-84a64f6db5af"]}
            />

        </>
    )
}

export  function Absenteeism(){
    return(
        <>
            <ProjectData
                header="Absenteeism Data Analysis"
                id="data1"
                desc="This analysis aims to find out the factors that contribute to people not showing up for thier doctor's appointments.In this project l used exploratory dataa analysis find out the patterns and correlations and give recommendations on what can be done to increase likelihood of showing up"
                tech="The main goal of the project was expoaratory data analysis.I first assessed the structure of the dataset then went on to carry exploratory data analysis to find the relationships between factors affecting appointment turn up "
                github="https://github.com/Stoichiometrical/Appointment-Absenteeism-Data-Analysis"
                recommendations={["So the main factors influencing whether or not a person will show up are : Type of Sickness, Age and whether they recieved SMS\n" ,
                    "Recommendations : The hospital should send more messages to remind people of thier appointments.More analysis can be directed towards why some people didnt recieve messages and what can be done about that","From our analysis we looked at the factors that influence a person turnup probability.We looked at the influence of objective factors like sickness then other incentives that might complement and influence a persons turn up. We found that people with high risk illnesses are more likely to turn up than the rest.These sicknesses are Hipertension and Dibetes.This is very true because failure to come for check up or to get treatment might be fatal so these people have to show up. Age is also another great contributor, and we see that vulnerable people(very young babies or very old people) are more likely to show up than the rest of the population. We however noticed that irregardless of sicknesss there were other factors that contributed.And from our analysis, sending people reminder SMS was more effective and resuted in more people turning up for thier appoinitments. Although,we might have expected that people with Scholarship might turn up more, they actaully turn up less.This might be because they dont bear the responsibility of paying and might not take appointments as serious because they can always book anytime without having to worry about getting money for the appointment"]}
                rec={true}
                images={[""]}


            />
        </>
    )
}

export  function WeRateDogs(){
    return(
        <>
            <ProjectData
                header="WeRateDogsData Analysis"
                id="data1"
                desc="In this project l analysed the WeRateDogs twitter account to find any patterns and interesting insights in the way thier followers like and talk about dogs."
                tech="I got tweets on the WeRateDogs twitter account using the Twitter API and then combined them with an image predictions dataset of the dogs on the account.I then cleaned the data to remove outliers ,symbols and make it tidy.Then finally saved the clean and formated data as a csv file, which l latter laoded into a dataframe and carried out exploratory data analysis and made several visualisations.From this l was able to get some insights which l compiled into a report.I used Python for the entire analysis including libraries like requests, seaborn, tweepy and pandas."
                github="https://github.com/Stoichiometrical/WeRateDogs-Data-Analysis"
                images={["/assets/rate.png"]}
                projectFiles={["Wrangling Report :https://github.com/Stoichiometrical/WeRateDogs-Data-Analysis/blob/main/wrangle_report.pdf ","Analysis Report :https://github.com/Stoichiometrical/WeRateDogs-Data-Analysis/blob/main/act_report.pdf"]}
            />
        </>
    )
}

