import  "./table.scss"
import MUITable from "./MUITable";

export default function Table(props){
    return(
        <>
            <div className="table">
                <div className="tWrapper">
                    <div className="text">{props.head}</div>
                    <MUITable className="table" project="The Steward" />

                </div>
            </div>


        </>
    )
}