import { useState } from "react";
import "./form.scss"


export default function SendMail() {
    const [email, setEmail] = useState("");
    const [header, setHeader] = useState("");
    const [message, setMessage] = useState("");
    const [errorMessage, setErrorMessage] = useState("");
    const [successMessage, setSuccessMessage] = useState("");

    const handleSubmit = (event) => {
        event.preventDefault();

        // Form validation
        if (!email) {
            setErrorMessage("Please enter your email");
            return;
        }

        if (!/\S+@\S+\.\S+/.test(email)) {
            setErrorMessage("Please enter a valid email address");
            return;
        }

        if (!header) {
            setErrorMessage("Please enter a header for your email");
            return;
        }

        if (!message) {
            setErrorMessage("Please enter a message");
            return;
        }

        // Send email using formsubmit.co
        const url = "https://formsubmit.co/ajax/d.gondo@alustudent.com";
        const data = new FormData();
        data.append("email", email);
        data.append("header", header);
        data.append("message", message);

        fetch(url, {
            method: "POST",
            body: data,
            headers: {
                Accept: "application/json",
            },
        })
            .then((response) => {
                if (!response.ok) {
                    throw new Error("Network response was not ok");
                }
                setSuccessMessage("Email sent successfully!");
                setEmail("");
                setHeader("");
                setMessage("");
                setErrorMessage("");
            })
            .catch((error) => {
                setErrorMessage("An error occurred while sending the email");
                console.error("An error occurred while sending the email", error);
            });
    };

    return (
        <form onSubmit={handleSubmit} className="send-mail-form">
            <h4>Connect With Me Here</h4>
            <div className="send-mail-input">
                <label htmlFor="email">Your Email:</label>
                <input
                    type="email"
                    id="email"
                    name="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                />
            </div>
            <div className="send-mail-input">
                <label htmlFor="header">Email Header:</label>
                <input
                    type="text"
                    id="header"
                    name="header"
                    value={header}
                    onChange={(e) => setHeader(e.target.value)}
                />
            </div>
            <div className="send-mail-input">
                <label htmlFor="message">Message:</label>
                <textarea
                    id="message"
                    name="message"
                    value={message}
                    onChange={(e) => setMessage(e.target.value)}
                />
            </div>
            {errorMessage && <p className="send-mail-error">{errorMessage}</p>}
            {successMessage && (
                <p className="send-mail-success">{successMessage}</p>
            )}
            <button type="submit" className="send-mail-button">
                Send
            </button>
        </form>
    );
}
